<template>
  <div ref="fullCanvas" style="width:100%;">
    <div class="canvasclass" ref="controlCanvas">    
    <canvas
      id="canvas"
      @mouseover="canvasOver($event)"
      @mousedown="canvasDown($event)"
      @mousemove="canvasMove($event)"
      @mouseup="canvasUp($event)"
      @mouseleave="canvasLeave($event)"
      ref="canvas"
      v-bind:style="{cursor:cursorstyle}"
    ></canvas>
    </div> 
    <!-- <router-link to="/foo">Go to Foo</router-link>
    <router-link to="/bar">Go to Bar</router-link>
    <router-link to="/user/hellowolrd">asdad</router-link>
    <div @click="goto">gotohellowolrd
    </div>
    <router-view>PS:写在这里,即router-view里的内容是不会显示在页面上的!</router-view> -->
  </div>
</template>

<script>

import eventVue from '../components/eventVue'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  mounted() {
  eventVue.$on("myFun",(message)=>{   
      this.colorstyle = message;   
      }),
  eventVue.$on("type",(message)=>{   
      this.type = message;
      if(message == "Ploygon" 
      // || message == "radomPloygon"
      ){
        this.openbox(message);
      }   
      }),
  eventVue.$on("clean",()=>{   
      this.clean();

      }),
  eventVue.$on("dele",()=>{   
      this.dele();

      }),
  eventVue.$on("savecav",()=>{   
      this.savedev();

      }),
  eventVue.$on("loadcav",(txtdata)=>{   
      this.loadcav(txtdata);

      }),
  this.show();
  },
  data(){
    return{
      colorstyle: 'rgb(0,0,0,1)',
      clickCanvasX: '',
      clickCanvasY: '',
      type: 'line',
      canvaswidth:450, // 默认画布大小
      canvasheight:350,
      canvasmargintop:100, // 默认画布margin高度
      canvasmargintopbackup:0, //在margin高度足够小后，这个用来添加备用 
      cursorstyle:"default", // 指针样式
      cursordirection:"", // 判断画布是往右边扩充还是左边扩充 
      cursorjudge:false, // 点击后是否是在改变画布状态下
      scrollchange:0, // 画布改变大小
      canvasMoveData:[], // 画布移动时存一个图形进栈
      canvasOrigindata:[],//画布开始移动后的坐标，
      polynumber:0,
      tempcanvax:0, // 记录任意多边形第一次生成后的鼠标up状态的位置，下同
      tempcanvay:0,
      tempcount:0, //记录任意多边形，以此判断多边形的生成
      tempdata:[], // 记录任意多边形的初始位置 后添加：包括每个图形的初始坐标（用于判断位移使用）
      tempupdata:[], //记录第一次后任意多边形每次鼠标up的坐标
      judge1:false,// 三个皆为判断任意多边形的，这个为判断是否画了起始边
      judge2:false, // 判断画了起始边后的每次画边
      judge3:false, // 判断点击UP的位置与移动的距离是否小于一个值（这里设3）
      alltypetemp:[], // 存放暂时点击流程下的图形
      alltype:[], // 存放所有图形以不断重新生成
      allRanPolyData:[], //存放暂时性的未完成的任意多边形
      ranpolyData:[], // 存放已经完成的任意多边形以供移动操作。
      clickjudge:false, // 判断是否进入选中状态
      clickjudge1:false, // 判断进入选中状态后开始移动
      tempnumber:Number, // 查找选中的是哪个图形
      clickdata:[], // 选中图形的位置
      key:[] //// 当绘画完成一个任意多边形时，将这个多边形的信息存入这个key里面，然后根据这个key里的参数再重新生成一个图形，并将其保存进alltype里面。
    }
  },
  methods:{
  // 页面滚动函数
  handleScroll(event){
    event = EventUtil.getEvent(event);
    var delta = EventUtil.getWheelDelta(event);
    this.scrollchange = 0;
    // this.$refs.controlCanvas.style.marginTop = `200px`
      if(delta==1){
        this.scrollchange = 20;
        if(this.canvaswidth>=1500||this.canvasheight>=1300){
          this.$alert('您扩充的画布太大，请重试！', '扩充画布', {
            confirmButtonText: '确定',
            callback: () => {
              return;
            }
          });
          return
        }
        this.canvaswidth+=this.scrollchange;
        this.canvasheight+=this.scrollchange;
        if(this.canvasmargintop>=20){
          this.canvasmargintop-=(this.scrollchange/2);
        }// 当margintop足够小时，给备用的减少或
        else{
          this.canvasmargintopbackup-=(this.scrollchange/2);
        }
        // console.log(this.$refs.controlCanvas.style.marginTop);
        // console.log(this.$refs.controlCanvas.style.marginLeft);
        if(this.$refs.fullCanvas.style.textAlign||this.$refs.controlCanvas.style.marginLeft){
          let tempstrwidth = parseInt(this.$refs.controlCanvas.style.marginLeft);
          this.$refs.controlCanvas.style.marginLeft =`${tempstrwidth-(this.scrollchange/2)}px`;
        }
      }else{
        this.scrollchange = -20;
        if(this.canvaswidth<=20||this.canvasheight<=20){
          this.$alert('您缩小的画布太小，请重试！', '缩小画布', {
            confirmButtonText: '确定',
            callback: () => {
              return;
            }
          });
          return
        }
        this.canvaswidth+=this.scrollchange;
        this.canvasheight+=this.scrollchange;
        // if(this.canvasmargintop<=20){

        // }
        this.canvasmargintop-=(this.scrollchange/2);
        // console.log(this.$refs.controlCanvas.style.marginLeft);
        if(this.$refs.fullCanvas.style.textAlign||this.$refs.controlCanvas.style.marginLeft){
          let tempstrwidth = parseInt(this.$refs.controlCanvas.style.marginLeft);
          this.$refs.controlCanvas.style.marginLeft =`${tempstrwidth-(this.scrollchange/2)}px`;
        }
      }
      this.show();
      this.generateAgainAll(this.alltype);
      this.generateAgainAll(this.allRanPolyData);
  },
  show(){
      // this.$store.commit('show')
      // console.log(this.$options);
      this.$store.commit('increment')
      // console.log(this.$store.state.count) // -> 1
      this.canvas = this.$refs.canvas;//指定canvas
      this.canvas.height = this.canvasheight;
      this.canvas.width = this.canvaswidth;
      this.ctx = this.canvas.getContext("2d");//设置2D渲染区域
      this.ctx.lineWidth = 1;//设置线的宽度
      this.$refs.controlCanvas.style.marginTop = `${(this.canvasmargintop+this.canvasmargintopbackup)<0?0:(this.canvasmargintop+this.canvasmargintopbackup)}px` // 默认高度；
      // console.log(this.canvasmargintopbackup);
      // console.log(this.canvasmargintop);
      // console.log(this.$refs.controlCanvas.style.marginTop);
      // this.ctx.beginPath();
      this.ctx.rect(0, 0, this.canvaswidth, this.canvasheight);
      this.ctx.stroke();
      // this.ctx.closePath();
  },
  canvasOver() {
    EventUtil.addHandler(document, "mousewheel", this.handleScroll,true);
    EventUtil.addHandler(document, "DOMMouseScroll", this.handleScroll,true);
  },
  canvasDown(e) {
      this.canvasMoveUse = true;
      this.canvasMoveData = [this.ctx,0,0,this.canvaswidth,this.canvasheight];// 初始化一个原图形，避免不执行鼠标move阶段
      // 进行画布伸缩
      if(this.cursorstyle!="default"){
        this.checkCavs();
        return 
      }
      this.clickCanvasX = e.clientX - e.target.offsetLeft;
      this.clickCanvasY = e.clientY - e.target.offsetTop + document.documentElement.scrollTop
      let value;
      let temp;
      // 判断是否是任意多边形
      if(this.type !='radomPloygon'){
        this.judge1 = false;
      }
      this.tempdata.push({x:this.clickCanvasX,y:this.clickCanvasY});

      if(this.judge1){
        this.ctx.moveTo(this.tempcanvax, this.tempcanvay);
        // 创建多边形后，赋予的点击生成边的功能
        this.ctx.strokeStyle = this.colorstyle;
        let temp = [this.ctx, this.tempupdata[this.tempupdata.length-1].x, this.tempupdata[this.tempupdata.length-1].y, this.clickCanvasX, this.clickCanvasY,this.ctx.strokeStyle,this.type];
        this.alltypetemp = new RadompolyNumber(...temp);
        this.alltypetemp.generate();
        this.ctx.stroke();
        // 自动缝合
        try {
          if(this.tempupdata[this.tempupdata.length-1].x==this.clickCanvasX&&this.tempupdata[this.tempupdata.length-1].y==this.clickCanvasY){
            let temp = [this.ctx, this.tempupdata[this.tempupdata.length-1].x, this.tempupdata[this.tempupdata.length-1].y, this.tempdata[0].x, this.tempdata[0].y,this.tempcount,this.ctx.strokeStyle];
            this.allRanPolyData.push(new RadompolyNumber(...temp));

            // 重新生成整个任意多边形
            
            let tempdata = [this.ctx,this.allRanPolyData[0].x+this.allRanPolyData[0].a,this.allRanPolyData[0].y+this.allRanPolyData[0].b,this.allRanPolyData[0].x,this.allRanPolyData[0].y,this.tempcount,this.ctx.strokeStyle];
            let newset = new RadompolyNumber(...tempdata);
            this.key = [];
            this.key.push(newset);
            for(let i=1;i<this.allRanPolyData.length;i++){
              this.key.push(this.allRanPolyData[i]);
            }
            let RanPolyType = new RadomPolyType(this.ctx,this.type,this.ctx.strokeStyle,0,...this.key);
            // let aaa = [this.ctx,this.type,this.ctx.strokeStyle,0,this.key]
            // console.log(aaa);
            this.alltype.push(RanPolyType);

            this.tempcanvax = 0;
            this.tempcanvay = 0;
            this.tempcount = 0;
            this.tempdata = [];
            this.tempupdata = [];
            this.allRanPolyData = [];
            this.alltypetemp = [];
            this.judge1 = false;
            this.judge2 = false;
            this.judge3 = false;
            return;
          }
          
          // console.log(this.tempupdata[this.tempupdata.length-2].x,this.tempupdata[this.tempupdata.length-2].y);
        } 
        catch (error) {
          // console.log(error);
        }
      }else{
        this.ctx.moveTo(this.clickCanvasX, this.clickCanvasY);
      }
        // 遍历每一个插进数组的数据的图像,为每一个生成一个矩形,然后判断点击的这个点是否在这个矩形里面,如果是,那就是可以选择他了
      for(let i=0 ;i<this.alltype.length;i++){
          if(this.alltype[i] instanceof Array){
            continue;
          }// 线条暂时无法做。。
          switch(this.alltype[i].type){
            case 'radius':
            this.ctx.beginPath();
            this.ctx.rect(this.alltype[i].x-this.alltype[i].a,this.alltype[i].y+this.alltype[i].b,this.alltype[i].a*2,-this.alltype[i].b*2);
            this.ctx.closePath();
            if(this.ctx.isPointInPath(this.clickCanvasX, this.clickCanvasY)){
              this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
              this.drawDashRect(this.ctx,this.alltype[i].x-this.alltype[i].a,this.alltype[i].y+this.alltype[i].b,this.alltype[i].a*2,-this.alltype[i].b*2);
              this.clickdata.push([this.alltype[i].x-this.alltype[i].a,this.alltype[i].y+this.alltype[i].b,this.alltype[i].a*2,-this.alltype[i].b*2]);
              this.ctx.strokeStyle = this.alltype[i].color;
              this.clickjudge = true;
              this.tempnumber = i;
              if(this.clickjudge1){
                if(this.clickdata[0][0]==this.alltype[i].x-this.alltype[i].a&&this.clickdata[0][1]==this.alltype[i].y+this.alltype[i].b&&this.clickdata[0][2]==this.alltype[i].a*2&&this.clickdata[0][3]==-this.alltype[i].b*2){
                  //do something
                  // console.log('can');
                }else{
                this.clickdata.splice(0,1);
                // console.log('last');
                this.clickjudge1 = false;
                }
              }else{
                // this.clickjudge = false;
              }
              return 
            }
            else{
              // 如果不是则清除掉虚线框
              this.generateAgainAll(this.alltype);
              this.clickjudge = false;
            }
            try {
              if(this.clickdata[0][0]==this.alltype[i].x-this.alltype[i].a&&this.clickdata[0][1]==this.alltype[i].y+this.alltype[i].b&&this.clickdata[0][2]==this.alltype[i].a*2&&this.clickdata[0][3]==-this.alltype[i].b*2){
              this.clickjudge1 = false;
              this.clickdata.splice(0,1);
              this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
            }
            } catch (error) {
              // console.log(error);
            }
            
            break;
            case 'rectangle':
            value = this.alltype[i].compare();
            this.ctx.beginPath();
            this.ctx.rect(value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny);
            this.ctx.closePath();
            if(this.ctx.isPointInPath(this.clickCanvasX, this.clickCanvasY)){
              this.cleanScreen(this.ctx);
              temp = this.alltype[i];
              this.alltype.splice(i,1);
              this.generateAgainAll(this.alltype);
              this.alltype.splice(i,0,temp);
              this.drawDashRect(this.ctx,value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny);
              this.ctx.strokeStyle = this.alltype[i].color;
              this.clickdata.push([value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny]);
              this.clickjudge = true;
              this.tempnumber = i;
              if(this.clickjudge1){
                if(this.clickdata[0][0]==value.minx&&this.clickdata[0][1]==value.miny&&this.clickdata[0][2]==value.maxx-value.minx&&this.clickdata[0][3]==value.maxy-value.miny){
                  //do something
                }else{
                this.clickdata.splice(0,1);
                this.clickjudge1 = false;
                }
              }
              return 
            }else{
              // 如果不是则清除掉虚线框
              // this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
              this.clickjudge = false;
            }
            try {
              if(this.clickdata[0][0]==value.minx&&this.clickdata[0][1]==value.miny&&this.clickdata[0][2]==value.maxx-value.minx&&this.clickdata[0][3]==value.maxy-value.miny){
              this.clickjudge1 = false;
              this.clickdata.splice(0,1);
              this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
            }
            } catch (error) {
              // console.log(error);
            }
            break;
            default:
            value = this.alltype[i].compare();
            this.ctx.beginPath();
            this.ctx.rect(value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny);
            this.ctx.closePath();
            if(this.ctx.isPointInPath(this.clickCanvasX, this.clickCanvasY)){
              this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
              this.drawDashRect(this.ctx,value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny);
              this.ctx.strokeStyle = this.alltype[i].color;
              this.clickdata.push([value.minx,value.miny,value.maxx-value.minx,value.maxy-value.miny]);
              this.clickjudge = true;
              this.tempnumber = i;

              if(this.clickjudge1){
                if(this.clickdata[0][0]==value.minx&&this.clickdata[0][1]==value.miny&&this.clickdata[0][2]==value.maxx-value.minx&&this.clickdata[0][3]==value.maxy-value.miny){
                  //do something
                }else{
                this.clickdata.splice(0,1);
                this.clickjudge1 = false;
                }
              }
              return 
            }else{
              // 如果不是则清除掉虚线框
              // this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
              this.clickjudge = false;
            }
            try {
              if(this.clickdata[0][0]==value.minx&&this.clickdata[0][1]==value.miny&&this.clickdata[0][2]==value.maxx-value.minx&&this.clickdata[0][3]==value.maxy-value.miny){
              this.clickjudge1 = false;
              // this.cleanScreen(this.ctx);
              if(this.alltype[i].type!='radomPloygon'){
                this.cleanScreen(this.ctx);
              }
              this.clickdata.splice(0,1);
              this.generateAgainAll(this.alltype);
              }
            } catch (error) {
              // console.log(error);
            }
          }
      }
      this.cleanScreen(this.ctx);
      this.generateAgainAll(this.alltype);
      this.generateAgainAll(this.allRanPolyData);
    },
    canvasMove(e) {
      const t = e.target;
      let canvasX;
      let canvasY;
      canvasX = e.clientX - t.offsetLeft;
      canvasY = e.clientY - t.offsetTop + document.documentElement.scrollTop;
      let shiftX = this.clickCanvasX - canvasX;
      let shiftY = this.clickCanvasY - canvasY;
      if (this.canvasMoveUse) {
        // 只有允许移动时调用
        // console.log('move');
        let temp = [];
        
        if(this.clickjudge){
          let value = this.alltype[this.tempnumber];
          let type = value.type;
          // if(value instanceof Array){
          //   type = value[0].type;
          // }
          let offset = {}; // 任意多边形偏移量
          let temparray = []; // 移动任意多边形
          if(this.clickjudge1){
              switch(type){
              case 'line':
              // this.cleanScreen(this.ctx);
              // this.generateAgainAll(this.alltype);
              // this.ctx.beginPath();
              // this.ctx.strokeStyle = this.colorstyle;
              // for(let i=0;i<value.length;i++){
              //   templine.push(value[i]);
              //   templine[i].a = shiftX;
              //   templine[i].b = shiftY;
              // }
              // temp = [this.ctx, canvasX, canvasY,shiftX, shiftY,this.ctx.strokeStyle,this.type];
              // this.alltypetemp.push(new Line(...temp));
              // for(let i=0;i<this.alltypetemp.length;i++){
              //   this.alltypetemp[i].generate();
              // }
              break;
              case 'beeline':
              this.ctx.beginPath();
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, canvasX-(0.5*value.a), canvasY-(0.5*value.b), value.a, value.b,this.ctx.strokeStyle,type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Beeline(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.stroke();
              break;
              case 'radius':
              // 生成一个之前一样的圆形;
              this.ctx.beginPath();
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, canvasX, canvasY, value.a, value.b,this.ctx.strokeStyle,type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new BezierEllipse2(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.stroke();
              break;
              case 'rectangle':
              this.ctx.beginPath();
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, canvasX-(0.5*value.a), canvasY-(0.5*value.b), value.a, value.b,this.ctx.strokeStyle,type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Rectangle(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.stroke();
              break;
              case 'star':
              this.ctx.beginPath();
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, canvasX, canvasY, value.a, value.b,this.ctx.strokeStyle,type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Star(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.stroke();
              break;
              case 'Ploygon':
              this.ctx.beginPath();
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, canvasX, canvasY, value.a, value.b,this.ctx.strokeStyle,type,value.polynumber];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Ploygon(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.stroke();
              break;
              case 'radomPloygon':
              this.ctx.beginPath();
              this.cleanScreen(this.ctx);
              this.generateAgainAll(this.alltype);
              offset = {shiftX,shiftY};
              temparray = [value.fir,...value.other];
              this.ctx.strokeStyle = value.color;
              temp = [this.ctx, value.type, value.color, offset,...temparray];
              this.ranpolyData = new RadomPolyType(...temp);
              this.generateAgainAll(this.ranpolyData);
              this.ranpolyData.generate();
              this.ctx.strokeStyle = value.color;
              this.ctx.stroke();
              break;
            }
          }else{
            return 
          }
          
        }else{
          switch(this.type){
          case 'line': 
            this.cleanScreen(this.ctx);
            this.ctx.strokeStyle = this.colorstyle;
            this.generateAgainAll(this.alltype);
            this.ctx.beginPath();
            this.ctx.strokeStyle = this.colorstyle;
            temp = [this.ctx, canvasX, canvasY,0, 0,this.ctx.strokeStyle,this.type];
            this.alltypetemp.push(new Line(...temp));
            
            for(let i=0;i<this.alltypetemp.length;i++){
              this.alltypetemp[i].generate();
            }
          break;
          case 'beeline':
              this.ctx.beginPath();
              this.ctx.strokeStyle = this.colorstyle;
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.ctx.strokeStyle,this.type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Beeline(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.strokeStyle = this.colorstyle;
              this.ctx.stroke();
              // console.log('beeline');
          break;
          case 'radius':
              this.ctx.beginPath();
              this.ctx.strokeStyle = this.colorstyle;
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.ctx.strokeStyle,this.type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new BezierEllipse2(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.strokeStyle = this.colorstyle;
              this.ctx.stroke();
          break;
          case 'rectangle':
              this.ctx.beginPath();
              this.ctx.strokeStyle = this.colorstyle;
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.ctx.strokeStyle,this.type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Rectangle(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.strokeStyle = this.colorstyle;
              this.ctx.stroke();
          break;
          case 'star':
              this.ctx.beginPath();
              this.ctx.strokeStyle = this.colorstyle;
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.ctx.strokeStyle,this.type];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Star(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.strokeStyle = this.colorstyle;
              this.ctx.stroke();
          break;
          case 'Ploygon':
              this.ctx.beginPath();
              this.ctx.strokeStyle = this.colorstyle;
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.ctx.strokeStyle,this.type,this.polynumber];
              this.cleanScreen(this.ctx);
              this.alltypetemp = new Ploygon(...temp);
              this.generateAgainAll(this.alltype);
              this.alltypetemp.generate();
              this.ctx.strokeStyle = this.colorstyle;
              this.ctx.stroke();
          break;
          case 'radomPloygon':
            this.ctx.beginPath();
            this.cleanScreen(this.ctx);
            this.ctx.strokeStyle = this.colorstyle;
            this.generateAgainAll(this.allRanPolyData);
            this.judge1 =true;
            if(this.judge2&&this.tempcanvax!=0){
              temp = [this.ctx, this.tempcanvax, this.tempcanvay, -shiftX, -shiftY,this.tempcount,canvasX-this.tempcanvax,canvasY-this.tempcanvay,this.ctx.strokeStyle,this.type];
            }else{
              temp = [this.ctx, canvasX, canvasY, shiftX, shiftY,this.tempcount,0,0,this.ctx.strokeStyle,this.type];
            }
            this.generateAgainAll(this.alltype);
            this.generateAgainAll(this.allRanPolyData);
            this.alltypetemp = new Radompoly(...temp);
            this.alltypetemp.generate();
            this.ctx.strokeStyle = this.colorstyle;
            this.ctx.stroke();
            break;
          }
          
            this.ctx.closePath();    

            // 罪魁祸首
            // this.generateAgainAll(this.alltype);
            // this.generateAgainAll(this.allRanPolyData);
        }
      }
            // 若不是在绘图状态下
      // 
      else{
        if(!this.clickjudge){
          if(this.cursorjudge){
            let checkChangeCanvas
            switch(this.cursordirection){
              case "left":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.canvasMoveData = [this.ctx,-shiftX,this.canvasOrigindata[1],this.canvasOrigindata[0]+this.canvaswidth-canvasX,this.canvasheight]
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return
                // break;
              case "right":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                // console.log(this.canvasMoveData);
                this.$refs.fullCanvas.style.textAlign = "left";
                this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                this.canvasMoveData = [this.ctx,0,this.canvasOrigindata[1],e.clientX-t.offsetLeft,this.canvasheight] // 左边暂时为0，右边应该就是变化的之类
                // console.log(shiftX,canvasX,e.clientX,t.offsetLeft)
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return
                // break;
              case "top":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.$refs.fullCanvas.style.textAlign = "left";
                this.canvasMoveData = [this.ctx,this.canvasOrigindata[0],-shiftY,this.canvaswidth,this.canvasOrigindata[1]+this.canvasheight-canvasY]
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return
                // break;
              case "down":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.$refs.fullCanvas.style.textAlign = "left";
                this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
                this.canvasMoveData = [this.ctx,this.canvasOrigindata[0],0,this.canvaswidth,e.clientY - t.offsetTop + document.documentElement.scrollTop]
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return 
                // break;
              case "lefttop":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.canvasMoveData = [this.ctx,-shiftX,-shiftY,this.canvasOrigindata[0]+this.canvaswidth-canvasX,this.canvasOrigindata[1]+this.canvasheight-canvasY]
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return 
                // break;
              case "righttop":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                // console.log(this.canvasMoveData);
                this.$refs.fullCanvas.style.textAlign = "left";
                this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                this.canvasMoveData = [this.ctx,0,-shiftY,e.clientX-t.offsetLeft,this.canvasOrigindata[1]+this.canvasheight-canvasY] // 左边暂时为0，右边应该就是变化的之类
                // console.log(shiftX,canvasX,e.clientX,t.offsetLeft)
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return
                // break;
              case "leftdown":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
                this.canvasMoveData = [this.ctx,-shiftX,0,this.canvasOrigindata[0]+this.canvaswidth-canvasX,e.clientY - t.offsetTop + document.documentElement.scrollTop];
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return 
                // break;
              case "rightdown":
                checkChangeCanvas = true;
                this.cleanScreen(this.ctx,checkChangeCanvas);
                this.$refs.fullCanvas.style.textAlign = "left";
                this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
                this.canvasMoveData = [this.ctx,0,0,e.clientX-t.offsetLeft,e.clientY - t.offsetTop + document.documentElement.scrollTop]
                this.drawDashRect(...this.canvasMoveData);
                this.ctx.stroke();
                this.ctx.closePath();
                return 
                // break;
            }
          }else{
            this.clickCanvasX = (e.clientX - e.target.offsetLeft)>(this.canvaswidth/2)?(e.clientX-this.canvaswidth-e.target.offsetLeft):(e.clientX - e.target.offsetLeft);
            this.clickCanvasY = (e.clientY - e.target.offsetTop + document.documentElement.scrollTop)>(this.canvasheight/2)?(e.clientY -this.canvasheight - e.target.offsetTop + document.documentElement.scrollTop):(e.clientY - e.target.offsetTop + document.documentElement.scrollTop);
            if(this.clickCanvasX<=1&&this.clickCanvasX>=0&&Math.abs(this.clickCanvasY)>=3){
              this.cursordirection = "left";
              this.cursorstyle = "w-resize";
            }
            if(this.clickCanvasX>=-1&&this.clickCanvasX<0&&Math.abs(this.clickCanvasY)>=3){
              this.cursorstyle = "w-resize";
              this.cursordirection = "right";
            }
            if(this.clickCanvasY<=1&&this.clickCanvasY>=0&&Math.abs(this.clickCanvasX)>=3){
              this.cursordirection = "top";          
              this.cursorstyle = "s-resize";
            }
            if(this.clickCanvasY>=-1&&this.clickCanvasY<0&&Math.abs(this.clickCanvasX)>=3){
              this.cursordirection = "down";
              this.cursorstyle = "s-resize";
            } 
            if(this.clickCanvasX<=2&&this.clickCanvasX>=0&&this.clickCanvasY<=2&&this.clickCanvasY>=0){
              this.cursordirection = "lefttop";
              this.cursorstyle = "se-resize";
            }
            if(this.clickCanvasX<=2&&this.clickCanvasX>=0&&this.clickCanvasY>=-2&&this.clickCanvasY<0){
              this.cursordirection = "leftdown";
              this.cursorstyle = "ne-resize";
            }
            if(this.clickCanvasX>=-2&&this.clickCanvasX<0&&this.clickCanvasY<=2&&this.clickCanvasY>=0){
              this.cursordirection = "righttop";
              this.cursorstyle = "ne-resize";
            }
            if(this.clickCanvasX>=-2&&this.clickCanvasX<0&&this.clickCanvasY>=-2&&this.clickCanvasY<0){
              this.cursordirection = "rightdown";
              this.cursorstyle = "se-resize";
            }

            if(Math.abs(this.clickCanvasX)>=3&&Math.abs(this.clickCanvasY)>=3){
                this.cursorstyle = "default";
            }
            
            }
          }
        
      }
    },
    canvasUp(e) {
      if(this.cursorjudge){
          const t = e.target;
          this.cleanScreen(this.ctx,1);
          switch(this.cursordirection){
              case "left":
                // 修改后，计算现画布离左边的位置，然后修改css
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${e.clientX-t.offsetLeft}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
                  this.canvasmargintop = this.canvasOrigindata[1];
                  this.confirmCavs();
                }
                return;
                // break;
              case "right":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
                  this.canvasmargintop = this.canvasOrigindata[1];
                  this.confirmCavs();
                }
                
                return;
                // break;
              case "top":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = this.canvasMoveData[2]==0?this.canvasmargintop:this.canvasMoveData[2];
                  this.confirmCavs();
                }
                return;
                // break;
              case "down":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = parseInt(this.$refs.controlCanvas.style.marginTop);
                  this.confirmCavs();
                }

                return;
                // break;
              case "lefttop":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${e.clientX-t.offsetLeft}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = this.canvasMoveData[2]==0?this.canvasmargintop:this.canvasMoveData[2];
                  this.confirmCavs();
                }
                return 
                // break;
              case "righttop":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = this.canvasMoveData[2]==0?this.canvasmargintop:this.canvasMoveData[2];
                  this.confirmCavs();
                }

                return
                // break;
              case "leftdown":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${e.clientX-t.offsetLeft}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = parseInt(this.$refs.controlCanvas.style.marginTop);
                  this.confirmCavs();
                }
                return 
                // break;
              case "rightdown":
                if(this.confirmCheckCavs()){
                  this.$refs.fullCanvas.style.textAlign = "left";
                  this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
                  this.$refs.controlCanvas.style.marginTop = `${this.canvasMoveData[2]==0?this.canvasOrigindata[1]:this.canvasMoveData[2]}px`;
                  this.canvasmargintop = parseInt(this.$refs.controlCanvas.style.marginTop);
                  this.confirmCavs();
                }
                return
                // break;
            }
      }
      // 如果tempdata与这个点太近，则把tempdata清空？问题时这个点与tempdata的关系：用于判断
      this.canvasMoveUse = false;
      const t = e.target;
      let canvasX;
      let canvasY;
      canvasX = e.clientX - t.offsetLeft;
      canvasY = e.clientY - t.offsetTop + document.documentElement.scrollTop;
      // 
      if(this.judge1){
        this.judge3 = this.sucker(this.ctx,this.tempdata[0].x, this.tempdata[0].y,canvasX,canvasY);
        // 自动吸附到起始点
        this.allRanPolyData.push(this.alltypetemp);
        if(this.judge3){
          let temp = [this.ctx, canvasX, canvasY, this.tempdata[0].x, this.tempdata[0].y,this.tempcount,this.ctx.strokeStyle,this.type];
          this.allRanPolyData.push(new RadompolyNumber(...temp));
          let tempdata = [this.ctx,this.allRanPolyData[0].x+this.allRanPolyData[0].a,this.allRanPolyData[0].y+this.allRanPolyData[0].b,this.allRanPolyData[0].x,this.allRanPolyData[0].y,this.tempcount];
          let newset = new RadompolyNumber(...tempdata);
          this.key = [];
          this.key.push(newset);
          for(let i=1;i<this.allRanPolyData.length;i++){
            this.key.push(this.allRanPolyData[i]);
          }
          let RanPolyType = new RadomPolyType(this.ctx,this.type,this.ctx.strokeStyle,0,...this.key);
          this.alltype.push(RanPolyType);
          this.tempcanvax = 0;
          this.tempcanvay = 0;
          this.tempcount = 0;
          this.tempdata = [];
          this.tempupdata = [];
          this.allRanPolyData = [];
          this.alltypetemp = [];
          this.judge1 = false;
          this.judge2 = false;
          this.judge3 = false;
        }else{
          this.tempupdata.push({x:canvasX,y:canvasY});  // 每次点击完毕，记录这个点
          let x = Math.pow(Math.abs(this.tempupdata[0].x-this.tempdata[0].x),2);
          let y = Math.pow(Math.abs(this.tempupdata[0].y-this.tempdata[0].y),2);
          let isshort = Math.sqrt(x+y); 
          // 如果第一个点击完毕的点与起始点太近，则重新画
          if(isshort<5){
            isshort = 0;
            this.cleanScreen(this.ctx); 
            this.tempcanvax = 0;
            this.tempcanvay = 0;
            this.tempcount = 0;
            this.tempdata = [];
            this.tempupdata = [];
            this.judge1 = false;
            this.judge2 = false;
            this.judge3 = false;
            return;
          }
          this.judge2 = true;
          this.tempcanvax = canvasX; // 赋值当前up的点
          this.tempcanvay = canvasY;
          this.tempcount++;
        }
        
      }else{
        try {
        this.judge3 = this.sucker(this.ctx,this.tempdata[0].x, this.tempdata[0].y,canvasX,canvasY);
          if(this.judge3){
          this.allRanPolyData = [];
          }
        } catch (error) {
          // console.log('这里会因为前面把东西都清空的原因而无法执行上面的try');
        }
      }
        if(this.clickjudge){
          if(this.clickjudge1){
            if(this.alltype[this.tempnumber].type!='radomPloygon'){
              if(this.alltypetemp.length!=0){
                this.alltype.push(this.alltypetemp);
                this.alltypetemp = [];
                this.alltype.splice(this.tempnumber,1);
              }
              this.clickjudge = false;
              this.clickjudge1 = false;
              this.clickdata = [];
            }else{
              // 偏移后需要改变原来的点以存进栈里
              if(this.ranpolyData.length!=0){
                let offset = this.ranpolyData.offset;
                this.ranpolyData.fir.x = this.ranpolyData.fir.x - offset.shiftX;
                this.ranpolyData.fir.y = this.ranpolyData.fir.y - offset.shiftY;
                for(let i=0;i<this.ranpolyData.other.length;i++){
                  this.ranpolyData.other[i].x = this.ranpolyData.other[i].x - offset.shiftX;
                  this.ranpolyData.other[i].y = this.ranpolyData.other[i].y - offset.shiftY;
                }
                this.ranpolyData.offset = {shiftX:0,shiftY:0};
                this.alltype.push(this.ranpolyData);
                this.alltype.splice(this.tempnumber,1);
                this.ranpolyData = [];
              }
              this.clickjudge = false;
              this.clickjudge1 = false;
              this.clickdata = [];
            }
            this.cleanScreen(this.ctx);
            // this.generateAgainAll(this.allRanPolyData);
            this.generateAgainAll(this.alltype);
            return
          }else{
            // console.log('clickjudge1');
            // console.log(this.alltype);
            this.clickjudge1 = true;
            return
          }
            
            
        }
      if(this.type!='radomPloygon'){
          if(this.alltypetemp.length!=0){
            this.alltype.push(this.alltypetemp);
            this.alltypetemp = [];
          }
          
      }
      this.cursorjudge = false;
      this.cleanScreen(this.ctx);
      this.generateAgainAll(this.alltype);
      this.generateAgainAll(this.allRanPolyData);
    },
    canvasLeave() {
      this.canvasMoveUse = false; 
      EventUtil.removeHandler(document, "mousewheel", this.handleScroll,true);
      EventUtil.removeHandler(document, "DOMMouseScroll", this.handleScroll,true);
    },
    clean() {
      this.canvas = this.$refs.canvas;//指定canvas
      this.ctx = this.canvas.getContext("2d");//设置2D渲染区域
      // this.ctx.fillStyle="#ffffff";  
      this.ctx.beginPath(); 
      this.ctx.clearRect(0, 0, this.canvaswidth, this.canvasheight);
      // this.ctx.fill();
      this.ctx.strokeStyle = "rgb(0,0,0,1)";
      this.ctx.rect(0, 0, this.canvaswidth, this.canvasheight);
      this.ctx.stroke();
      this.ctx.closePath();  
      this.polynumber = 0;
      this.tempcount = 0;
      this.judge1 = false;
      this.judge2 = false;
      this.judge3 = false;
      this.clickjudge = false;
      this.clickjudge1 = false;
      this.tempdata = [];
      this.tempupdata = [];
      this.tempcanvax = 0;
      this.tempcanvay = 0;
      this.alltype = [];
      this.type = 'line';
    },
    cleanScreen(ctx,checkChangeCanvas) {
    // ctx.fillStyle="#ffffff";  
    ctx.beginPath();  
    if(!checkChangeCanvas){
      ctx.clearRect(0, 0, this.canvaswidth, this.canvasheight);
      ctx.strokeStyle = "rgb(0,0,0,1)";
      ctx.rect(0, 0, this.canvaswidth, this.canvasheight);
      ctx.stroke();
    }else{
      ctx.clearRect(0, 0, 1400, 1200)
    }
    // ctx.fill();
    
    ctx.closePath();
    },
    openbox(msg) {
      this.$prompt('请输入边数', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^((0?[3-9])|((1)[0-9])|20)$/,
          inputErrorMessage: '请输入大于等于3小于等于20的整数'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '你输入的边数是: ' + value,
          });
          if(msg == 'Ploygon') {
            this.polynumber = value;
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
          this.type = 'line';
        });
    },
    //x,y:开始绘制的点，a,b:最后停止的点
    sucker(ctx,x,y,a,b){
    ctx.beginPath();
    ctx.arc(x,y,5,0,Math.PI * 2);
    ctx.closePath();
    let dis = Math.sqrt((a - x) * (a - x) + (b - y) * (b - y));
    if(dis <= 5){
        return true;
    }
    return false;
    },
    drawDashLine(ctx,[x1, y1], [x2, y2], step = 5){
    const x = x2 - x1,
        y = y2 - y1,
        count = Math.floor(Math.sqrt(x * x + y * y) / step),
        xv = x / count,
        yv = y / count;
    ctx.beginPath();
    for (let i = 0; i < count; i ++) {
        if (i % 2 === 0) {
            ctx.moveTo(x1, y1);
        } else {
            ctx.lineTo(x1, y1);
        }
      x1 += xv;
      y1 += yv;
    }
    ctx.lineTo(x2, y2);
    },
    drawDashRect(ctx,left, top, width, height, step = 5){
    ctx.strokeStyle = 'rgb(0,0,0,1)';
    this.drawDashLine(ctx,[left, top], [left + width, top], step);
    ctx.stroke();
    this.drawDashLine(ctx,[left + width, top], [left + width, top + height], step);
    ctx.stroke();
    this.drawDashLine(ctx,[left + width, top + height], [left, top + height], step);
    ctx.stroke();
    this.drawDashLine(ctx,[left, top + height], [left, top], step);
    ctx.stroke();
    return true;
    },
    generateAgainAll(data){
      let value = data;
      let temp;
      for(let i=0;i<value.length;i++){
        if(value[i] instanceof Array){
          temp = value[i];
          this.ctx.moveTo(temp[0].x,temp[0].y);
          for(let j=0;j<temp.length;j++){
            this.ctx.lineTo(temp[j].x, temp[j].y);
            temp[j].generate();
          }
          temp = [];
          this.ctx.beginPath();
          this.ctx.strokeStyle = "rgb(0,0,0,1)";
          this.ctx.stroke();
          this.ctx.closePath();
        }else{
          this.ctx.beginPath();
          this.ctx.strokeStyle = value[i].color;
          this.ctx.stroke();
          this.ctx.strokeStyle = value[i].color;
          value[i].generate();
          this.ctx.strokeStyle = value[i].color;
          this.ctx.stroke();
          this.ctx.closePath();
        }        
      }
    },
    dele(){
      if(!this.clickjudge&&!this.clickjudge1){
        this.$notify({
          title: '删除图形',
          message: '您未选定图形删除'
        });
      }else{
        this.alltype.splice(this.tempnumber,1);
        this.cleanScreen(this.ctx);
        this.generateAgainAll(this.alltype);
        this.clickjudge = false;
        this.clickjudge1 = false;
        this.clickdata = [];
        // console.log(this.tempnumber);
      }
    },
    // 点击图形开始进行伸缩变化
    checkCavs(){
      this.canvasMoveUse = false;
      this.cursorjudge = true;
      this.canvas.height = 1200; // 默认
      this.canvas.width = 1400;   // 默认
      let tempnumer = parseInt(this.$refs.controlCanvas.style.marginLeft);
      this.$refs.controlCanvas.style.marginLeft = `0px`;
      let tempupdown = parseInt(this.$refs.controlCanvas.style.marginTop);
      // console.log(tempupdown);
      this.$refs.controlCanvas.style.marginTop = `0px`;
      this.canvasmargintop = 0;
      this.canvasmargintopbackup = 0;
      this.canvasOrigindata = [tempnumer?tempnumer:(1400-this.canvaswidth)/2,tempupdown,this.canvaswidth,this.canvasheight];// 记录当前绘图的左边点和上边点,大小
      this.drawDashRect(this.ctx,...this.canvasOrigindata);
      this.ctx.stroke();
    },
    // 完成图形的伸缩变化后，判断是否过大过小
    confirmCheckCavs(){
      this.cursorstyle = "default";
      this.cursordirection = "";
      this.cursorjudge = false;
      // console.log(this.canvasMoveData[3],this.canvasMoveData[4]);
      if(this.canvasMoveData[3]>=1500||this.canvasMoveData[4]>=1300){
        this.$alert('您扩充的画布太大，请重试！', '扩充画布', {
            confirmButtonText: '确定',
            callback: () => {
              return;
            }
        });
        this.$refs.controlCanvas.style.marginTop = this.canvasOrigindata[1];
        this.canvasmargintop = this.canvasOrigindata[1]==0?this.canvasmargintop:this.canvasOrigindata[1];
        this.$refs.controlCanvas.style.marginLeft = this.canvasOrigindata[0];
        this.ctx.beginPath();
        this.ctx.rect(0,0, this.canvaswidth, this.canvasheight);
        this.canvasMoveData = [];
        this.ctx.stroke();
        this.ctx.closePath();
        this.generateAgainAll(this.alltype);
        this.generateAgainAll(this.allRanPolyData);
        return false;
      }
      if(this.canvasMoveData[3]<=20||this.canvasMoveData[4]<=20){
        this.$alert('您缩小的画布太小，请重试！', '缩小画布', {
            confirmButtonText: '确定',
            callback: () => {
              return;
            }
          });
          this.$refs.controlCanvas.style.marginTop = `${this.canvasOrigindata[1]}px`;
          this.canvasmargintop = this.canvasOrigindata[1]==0?this.canvasmargintop:this.canvasOrigindata[1];
          this.$refs.controlCanvas.style.marginLeft = `${this.canvasOrigindata[0]}px`;
          this.ctx.beginPath();
          this.ctx.rect(0,0, this.canvaswidth, this.canvasheight);
          this.canvasMoveData = [];
          this.ctx.stroke();
          this.ctx.closePath();
          this.generateAgainAll(this.alltype);
          this.generateAgainAll(this.allRanPolyData);
          return false;
      }
      return true;
    },
    // 完成图形的伸缩变化后，确定图形并且保存到画布里
    confirmCavs(){
      this.cursorstyle = "default";
      this.cursordirection = "";
      this.cursorjudge = false;
      this.canvaswidth = this.canvasMoveData[3];
      this.canvasheight = this.canvasMoveData[4];
      this.canvas.height = this.canvasMoveData[4];
      this.canvas.width = this.canvasMoveData[3];
      this.ctx.beginPath();
      this.ctx.rect(0,0, this.canvasMoveData[3], this.canvasMoveData[4]);
      this.canvasMoveData = [];
      this.ctx.stroke();
      this.ctx.closePath();
      this.generateAgainAll(this.alltype);
      this.generateAgainAll(this.allRanPolyData);
    },
    savedev() {
      let tempCav = [this.$refs.controlCanvas.style.marginTop,this.canvasmargintop,this.$refs.controlCanvas.style.marginLeft,this.canvaswidth,this.canvasheight,this.$refs.fullCanvas.style.textAlign]
      if(!this.alltype.length){
        this.$alert('画布中并没有图形，无需保存！', '保存图形', {
          confirmButtonText: '确定',
          callback: () => {
            return;
          }
        });
      }else{
        this.cleanScreen(this.ctx);
        this.generateAgainAll(this.alltype);
        this.generateAgainAll(this.allRanPolyData);
        // let MIME_TYPE = "txt";
        // let imgURL = this.canvas.toDataURL("image/png");
        // 下载txt文件，里面含有json格式后的this.alltype数据与图片数据。
        // let temp = imgURL;
        // let temp = JSON.stringify(this.alltype)+imgURL;
        let temp = JSON.stringify(this.alltype)+tempCav; // 不需要base64数据先把- -
        let linkNode = document.createElement('a');
        linkNode.download = "filename";
        linkNode.style.display = 'none';
        // 利用Blob对象将字符内容转变成二进制数据
        let blob = new Blob([temp]);
        linkNode.href = URL.createObjectURL(blob);
        // 点击
        document.body.appendChild(linkNode);
        linkNode.click();
        // 移除
        document.body.removeChild(linkNode);
      }
    },
    loadcav(txtdata) {
      // 处理传上来的txt文件，分离前面的json和后面的base64数据
      let timesposition = txtdata.lastIndexOf("]");
      let temtimes = txtdata.substring(timesposition+1,txtdata.length).split(",");
      // txtdata = txtdata.split("data");
      // let canvasdata = "data"+txtdata[1];
      try {
      let temptypedata  = JSON.parse(txtdata.substring(0,timesposition+1));
      let stacktype = [];
      let temp = [];
      for(let i=0;i<temptypedata.length;i++){
        if(temptypedata[i] instanceof Array){
          let linevalue = temptypedata[i];
          let templine = [];
          for(let j=0,linlen=linevalue.length;j<linlen;j++){
            temp = [this.ctx,linevalue[j].x,linevalue[j].y,linevalue[j].a,linevalue[j].b,linevalue[j].color,linevalue[j].type];
            templine.push(new Line(...temp));
          }
          stacktype.push(templine);
        }
        switch(temptypedata[i].type){
          case "rectangle":
            temp = new Rectangle(this.ctx,temptypedata[i].x,temptypedata[i].y,temptypedata[i].a,temptypedata[i].b,temptypedata[i].color,temptypedata[i].type);
            stacktype.push(temp);
            break;
          case "radius":
            temp = new BezierEllipse2(this.ctx,temptypedata[i].x,temptypedata[i].y,temptypedata[i].a,temptypedata[i].b,temptypedata[i].color,temptypedata[i].type);
            stacktype.push(temp);
            break;
          case "beeline":
            temp = new Beeline(this.ctx,temptypedata[i].x,temptypedata[i].y,temptypedata[i].a,temptypedata[i].b,temptypedata[i].color,temptypedata[i].type);
            stacktype.push(temp);
            break;
          case "star":
            temp = new Star(this.ctx,temptypedata[i].x,temptypedata[i].y,temptypedata[i].a,temptypedata[i].b,temptypedata[i].color,temptypedata[i].type);
            stacktype.push(temp);
            break;
          case "Ploygon":
            temp = new Ploygon(this.ctx,temptypedata[i].x,temptypedata[i].y,temptypedata[i].a,temptypedata[i].b,temptypedata[i].color,temptypedata[i].type,temptypedata[i].polynumber);
            stacktype.push(temp);
            break;
          case "radomPloygon":
            temp = new RadomPolyType(this.ctx,temptypedata[i].type,temptypedata[i].color,temptypedata[i].offset,temptypedata[i].fir,...temptypedata[i].other);
            stacktype.push(temp);
            break;
        }
      }
      this.alltype = stacktype;

      if(temtimes[3]>=1500||temtimes[4]>=1200){
        this.$alert('您加载的画布太大或太小，请重试！', '加载画布', {
          confirmButtonText: '确定',
          callback: () => {
            return;
          }
        });
        return 
      }
      this.canvaswidth = parseInt(temtimes[3]);
      this.canvasheight=parseInt(temtimes[4]);
      // this.$refs.controlCanvas.style.marginTop = temtimes[0];
      this.canvasmargintop = parseInt(temtimes[1]);
      if(temtimes[5].length!=0){
        this.$refs.fullCanvas.style.textAlign = "left";
        this.$refs.controlCanvas.style.marginLeft = temtimes[2].length==0?"475px":temtimes[2];
      }
      // console.log(typeof(this.canvaswidth),typeof(this.canvasheight));
      this.cleanScreen(this.ctx);
      this.show();
      this.generateAgainAll(this.alltype);
      } catch (error) {
        this.$alert('您加载的图形并非在此页面保存下的，请重试！', '加载画布', {
          confirmButtonText: '确定',
          callback: () => {
            return;
          }
        });
        return 
      }
    },
  }
}

class Line{
  constructor(ctx, x, y,a,b,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;
        }
        generate(){
          this.ctx.lineTo(this.x, this.y);
          this.ctx.strokeStyle = this.color;
          this.ctx.stroke();
        }
        compare() {
          return 1;
        }
}

class BezierEllipse2{
        constructor(ctx, x, y, a, b,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;

        }
        generate(){
          let k = .5522848;
          let ox = this.a * k; // 水平控制点偏移量
          let oy = this.b * k; // 垂直控制点偏移量
          let ctx = this.ctx;
          ctx.beginPath();
          ctx.strokeStyle = this.color;
          ctx.moveTo(this.x - this.a, this.y);
          ctx.bezierCurveTo(this.x - this.a, this.y - oy, this.x - ox, this.y - this.b, this.x, this.y - this.b);
          ctx.bezierCurveTo(this.x + ox, this.y - this.b, this.x + this.a, this.y - oy, this.x + this.a, this.y);
          ctx.bezierCurveTo(this.x + this.a, this.y + oy, this.x + ox, this.y + this.b, this.x, this.y + this.b);
          ctx.bezierCurveTo(this.x - ox, this.y + this.b, this.x - this.a, this.y + oy, this.x - this.a, this.y);
          ctx.closePath();
        }
        // ctx.stroke();
    }
class Rectangle{
        constructor(ctx, x, y, a, b,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;
        }
        generate(){
          let ctx = this.ctx;
          ctx.beginPath();
          ctx.rect(this.x,this.y,this.a,this.b)
          ctx.strokeStyle = this.color;
          ctx.stroke();
          ctx.closePath();
        }
        compare(){
          let minx,miny,maxx,maxy;
          minx = this.x+this.a;
          maxx = this.x;
          miny = this.y+this.b;
          maxy = this.y;
          return{
            minx,miny,maxx,maxy
          }
        
        }
        // ctx.stroke();
    }

class Star{
        constructor(ctx, x, y, a, b,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;
          this.offsetx = [];
          this.offsety = [];
        }
        generate(){
          //设置是个顶点的坐标，根据顶点制定路径   
          let ctx = this.ctx;
          this.offsetx = [];
          this.offsety = [];
          ctx.beginPath(); 
          ctx.strokeStyle = this.color;
          for (let i = 0; i < 6; i++) {
              ctx.lineTo(Math.cos((18 + i * 72) / 180 * Math.PI)*this.a+this.x,-Math.sin((18 + i * 72) / 180 * Math.PI)*this.b+this.y);
              ctx.lineTo(Math.cos((54+i*72)/180*Math.PI)*(2/5*this.a)+this.x,-Math.sin((54+i*72)/180*Math.PI)*(2/5*this.b)+this.y);
              this.offsetx.push(Math.cos((18 + i * 72) / 180 * Math.PI)*this.a+this.x);
              this.offsetx.push(Math.cos((54+i*72)/180*Math.PI)*(2/5*this.a)+this.x);
              this.offsety.push(-Math.sin((18 + i * 72) / 180 * Math.PI)*this.b+this.y);
              this.offsety.push(-Math.sin((54+i*72)/180*Math.PI)*(2/5*this.b)+this.y);
          }
          ctx.restore();
          ctx.closePath();
        }
        compare(){
          let minx,miny,maxx,maxy;
          let tempx = this.offsetx;
          let tempy = this.offsety;
          tempx = maopao(tempx);
          tempy = maopao(tempy);
          minx = tempx[0];
          maxx = tempx[tempx.length-1];
          miny = tempy[0];
          maxy = tempy[tempy.length-1];
          return{
            minx,miny,maxx,maxy
          }
        
        }
        // ctx.stroke();
    }

class Ploygon{
        constructor(ctx, x, y, a, b,color,type,polynumber) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;
          this.polynumber = polynumber;
          this.offsetx = [];
          this.offsety = [];
        }
        generate(){
          //设置是个顶点的坐标，根据顶点制定路径   
          let ctx = this.ctx;
          let num = this.polynumber;
          let ang = Math.PI*2/num //旋转的角度
          this.offsetx = [];
          this.offsety = [];
          ctx.save();
          ctx.translate(this.x,this.y);
          ctx.strokeStyle = this.color;
          ctx.moveTo(0,-this.b);
          ctx.beginPath();
          for(let i=0;i<num;i++){
            ctx.rotate(ang);
            ctx.lineTo(0,-this.b);
            this.offsetx.push((-this.b*Math.sin(ang*(i+1)))+this.x);
            this.offsety.push((-this.b*Math.cos(ang*(i+1)))+this.y);
          }
          ctx.closePath();
          ctx.restore();
        }
        compare(){
          let minx,miny,maxx,maxy;
          let tempx = this.offsetx;
          let tempy = this.offsety;
          tempx = maopao(tempx);
          tempy = maopao(tempy);
          minx = tempx[0];
          maxx = tempx[tempx.length-1];
          miny = tempy[0];
          maxy = tempy[tempy.length-1];
          return{
            minx,miny,maxx,maxy
          }
        }
    }

class Beeline{
        constructor(ctx, x, y, a, b,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.type = type;
          this.color = color;
        }
        generate(){
          //设置是个顶点的坐标，根据顶点制定路径   

          let ctx = this.ctx;
          ctx.beginPath();
          ctx.strokeStyle = this.color;
          ctx.moveTo(this.x,this.y);
          ctx.lineTo(this.x+this.a,this.y+this.b);
          ctx.closePath();
        }
        
        // 按偏移的角度生成一个高度为5的长方形来
        compare(){
          let minx,miny,maxx,maxy;
          minx = Math.min(this.x,this.x+this.a);
          maxx = Math.max(this.x,this.x+this.a);
          miny = Math.min(this.y,this.y+this.b);
          maxy = Math.max(this.y,this.y+this.b);
          return{
            minx,miny,maxx,maxy
          }
        }
  
}

class Radompoly{
        constructor(ctx, x, y, a, b,tempcount,mox,moy,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.mox = mox;
          this.moy = moy;
          this.tempcount = tempcount;
          this.type = type;
          this.color = color;
        }
        generate(){
          //设置是个顶点的坐标，根据顶点制定路径   
        

          let ctx = this.ctx;
            ctx.beginPath();
            ctx.moveTo(this.x,this.y);
            if(this.tempcount!=0){
              ctx.lineTo(this.mox+this.x,this.moy+this.y);
            }else{
              ctx.lineTo(this.x+this.a,this.y+this.b);
            }
            ctx.strokeStyle = this.color
            ctx.closePath();
        }
}

class RadompolyNumber{
  constructor(ctx, x, y, a, b,tempcount,mox,moy,color,type) {
          this.ctx = ctx;
          this.x = x;
          this.y = y;
          this.a = a;
          this.b = b;
          this.mox = mox;
          this.moy = moy;
          this.tempcount = tempcount;
          this.type = type;
          this.color = color;
        }
         generate(){
          this.ctx.beginPath();
          this.ctx.moveTo(this.x,this.y);
          this.ctx.lineTo(this.a, this.b);
          this.ctx.closePath();
        }
}

class RadomPolyType{
  constructor(ctx,type,color,offset,fir,...other) {
          this.ctx = ctx;
          this.fir = fir;
          this.other = other;
          this.type = type;
          this.color = color;
          if(offset==0){
            this.offset = {shiftX:0,shiftY:0};
          }else{
            this.offset = offset;
          }
        }
         generate(){
          this.ctx.beginPath();
          this.ctx.moveTo(this.fir.x-this.offset.shiftX,this.fir.y-this.offset.shiftY);
          this.ctx.lineTo(this.other[0].x-this.offset.shiftX, this.other[0].y-this.offset.shiftY);
          for(let i=1;i<this.other.length;i++){
            this.ctx.lineTo(this.other[i].x-this.offset.shiftX, this.other[i].y-this.offset.shiftY);
          }
          this.ctx.closePath();
        }
        compare(){
          // 比较大小
          let minx,miny,maxx,maxy;
          let tempx = [this.fir.x-this.offset.shiftX];
          let tempy = [this.fir.y-this.offset.shiftY];
          for (let i = 0; i < this.other.length; i++) {
            tempx.push(this.other[i].x-this.offset.shiftX);
            tempy.push(this.other[i].y-this.offset.shiftY);
          }
          tempx = maopao(tempx);
          tempy = maopao(tempy);
          minx = tempx[0];
          maxx = tempx[tempx.length-1];
          miny = tempy[0];
          maxy = tempy[tempy.length-1];
          return{
            minx,miny,maxx,maxy
          }
        }
}


function maopao(arr){
  for(let i=0;i<arr.length;i++){
    for(let j=0;j<arr.length-1-i;j++){
        if(arr[j]>arr[j+1]){
            let temp = arr[j];
            arr[j]=arr[j+1];
            arr[j+1]=temp
        }
    }
  }
  return arr;
}


//判断浏览器类型
var client = function () {
        var engine = {
            ie: 0,
            gecko: 0,
            webkit: 0,
            khtml: 0,
            opera: 0,
            ver: null
        };
        return {
            engine: engine
        };
    }();

var EventUtil = {
    getEvent: function (event) {
        return event ? event : window.event;
    },
    addHandler: function (element, type, handler) {
        if (element.addEventListener) {
            element.addEventListener(type, handler, false);
        } else if (element.attachEvent) {
            element.attachEvent("on" + type, handler);
        } else {
            element["on" + type] = handler;
        }
    },
    removeHandler: function(element, type, handler) {
        if (element.addEventListener) {
            element.removeEventListener(type, handler, false);
        } else if (element.attachEvent) {
            element.detachEvent("on" + type, handler);
        } else {
            element["on" + type] = handler;
        }
    },
    getWheelDelta: function (event) {
        var driect=null;

        if (event.wheelDelta) {
            driect=(client.engine.opera && client.engine.opera < 9.5 ? -event.wheelDelta : event.wheelDelta);
        } else {
            driect=-event.detail * 40;
        }
        return (driect>0?1:-1);
    }
};
