<template>
  <div>
    <el-row :gutter="12">
    <el-col :span="8">
        <el-card shadow="hover">
        <el-container>
        <el-main>
          <div>
            <!-- <div><el-button icon="el-icon-edit" circle>铅笔</el-button>
              </div>
            <div><el-button icon="el-icon-help" circle>橡皮擦</el-button>
              </div> -->
            <div class="flexColumn">
              <el-button @click="clean()" size="mini" icon="el-icon-smoking" round>清空画布</el-button>
              <el-button @click="dele()"  icon="el-icon-delete" size="mini" round>删除图形</el-button>
              <el-button @click="savecav()"  icon="el-icon-folder-add" size="mini" round>保存画布</el-button>
              <div class="flexColumnchild">
              <el-upload action="/"
                      ref="upload"
                  accept=".txt"			                      
                  :before-upload="beforeUpload"		                         
                  :default-file-list="this.fileList"
                  >
              <el-button  style="font-size:12px;width:97px;margin-left:10px;height: 40px;" icon="el-icon-folder-opened" size="medium" round>加载画布</el-button>
              </el-upload>
              </div>
              </div>
        </div>
        </el-main>
        <el-footer style="height:30px;">工具</el-footer>
      </el-container>
        </el-card>
    </el-col>
    <el-col :span="8">
        <el-card shadow="hover">
        <el-container>
        <el-main>
          <div style="line-height: 16px;">
          <div>
          <el-button :autofocus="true" @click="line()" size="small">线条</el-button>
          <el-button @click="beeline()" size="small">直线</el-button>
          <el-button @click="star()" size="small">五角星</el-button>
          </div>
          <div>
            <el-button @click="rectangle()" size="small">矩形</el-button>
          <el-button @click="radius()" size="small">圆形</el-button>
          <el-button @click="Ploygon()" size="small">多边形</el-button>
          <el-button @click="radomPloygon()" size="small">任意多边形</el-button>
          </div>
          </div>
        </el-main>
        <el-footer style="height:30px;">形状</el-footer>
      </el-container>
        </el-card>
    </el-col>
    <el-col :span="6">
        <el-card shadow="hover">
        <el-container>
        <el-main style="padding:8.5px;">
          <div class="colorstyle">
            <div class="colorstylechild"> 
          <el-button size="mini" class="test" @click="color1($event)" :style="styleObj[0]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[1]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[2]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[3]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[4]" ></el-button>
          </div>
            <div class="colorstylechild">
          <el-button size="mini" @click="color1($event)" :style="styleObj[5]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[6]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[7]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[8]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[9]" ></el-button>
          </div>
          <div class="colorstylechild">
          <el-button size="mini" @click="color1($event)" :style="styleObj[10]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[11]" ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[12]"  ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[13]"  ></el-button>
          <el-button size="mini" @click="color1($event)" :style="styleObj[14]"  ></el-button>
          </div>
          </div>
        </el-main>
        <el-footer style="height:30px;">颜色</el-footer>
      </el-container>
        </el-card>
    </el-col>
    </el-row>

  </div>
</template>

<script>
import eventVue from '../components/eventVue'
export default {
  name: 'toolBar',
  props: {
    msg: String
  },
  beforeCreate() {
			// 读取文件
			FileReader.prototype.reading = function ({encode}) {
				let bytes = new Uint8Array(this.result);    //无符号整型数组
				let text = new TextDecoder(encode || 'UTF-8').decode(bytes);
				return text;
			};
			/* 重写readAsBinaryString函数 */
			FileReader.prototype.readAsBinaryString = function (f) {
				if (!this.onload)       //如果this未重写onload函数，则创建一个公共处理方式
					this.onload = () => {  //在this.onload函数中，完成公共处理
						// let rs = this.reading();
						// console.log(rs);
					};
				this.readAsArrayBuffer(f);  //内部会回调this.onload方法
			};
},
  mounted() {
  this.show();
  },
  data(){
    return{
      act:'1',
      fileList:"",
      styleObj:[
        { 
          value:'1',
          backgroundColor:'rgb(0,0,0,1)'
        },
        { 
          value:'2',
          backgroundColor:'rgb(127,127,127,1)'
        },
        { 
          value:'3',
          backgroundColor:'rgb(136,0,21,1)'
        },
        { 
          value:'4',
          backgroundColor:'rgb(120,150,30,1)'
        },
        { 
         value:'5',
          backgroundColor:'rgb(237,28,36,1)'
        },
        { 
          value:'6',
          backgroundColor:'rgb(255,242,0,1)'
        },
        { 
          value:'7',
          backgroundColor:'rgb(255,127,39,1)'
        },
        { 
          value:'8',
          backgroundColor:'rgb(34,177,76,1)'
        },
        { 
          value:'9',
          backgroundColor:'rgb(0,162,232,1)'
        },
        { 
          value:'10',
          backgroundColor:'rgb(63,72,204,1)'
        },
        { 
          value:'11',
          backgroundColor:'rgb(163,73,164,1)'
        },
        { 
          value:'12',
          backgroundColor:'rgb(255,255,255,1)'
        },
        { 
          value:'13',
          backgroundColor:'rgb(152,217,234,1)'
        },
        { 
          value:'14',
          backgroundColor:'rgb(30,152,164,1)'
        },
      ],
      // styleObj1:{backgroundColor:'red'},
    }
  },
  
  methods:{
  beforeUpload(file){
        this.fileList = [file]
        // 读取数据
        this.read(file);
        return false
    },
    read(f) {
			let rd = new FileReader();
			rd.onload = () => {  
            //this.readAsArrayBuffer函数内，会回调this.onload函数。在这里处理结果
        let cont = rd.reading({encode: 'GBK'});
        eventVue.$emit('loadcav',cont);
				// console.log(e);
				let  formerData = this.textData;
				this.textData = formerData + "\n" + cont;
			};
			rd.readAsBinaryString(f);
    },
  
  show(){
      // console.log('hellowolrd');
  },
  color1(e){
    eventVue.$emit("myFun",e.currentTarget.style.backgroundColor)
    // e.currentTarget.style.border = '1px solid rgb(3, 33, 63)';
    // console.log(e.currentTarget.style);
  },
  line() {
    eventVue.$emit("type","line");
  },
  beeline() {
    eventVue.$emit("type","beeline");
  },
  radius() {
    eventVue.$emit("type","radius");
  },
  rectangle() {
    eventVue.$emit("type","rectangle");  
  },
  star() {
    eventVue.$emit("type","star");  
  },
  Ploygon() {
    eventVue.$emit("type","Ploygon");  
  },
  radomPloygon() {
    eventVue.$emit("type","radomPloygon");  
  },
  clean() {
    eventVue.$emit("clean",true);
  },
  dele() {
    eventVue.$emit('dele',true);
  },
  savecav() {
    eventVue.$emit('savecav',true);
  },
  loadcav() {
    // eventVue.$emit('loadcav',true);
  },
  
}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .flexColumn{
        display: flex;
        justify-content: center;
        /* flex-wrap:wrap; */
        margin-bottom:23px;
    }
      .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 30px;
    }
    .colorstyle{
      /* padding-top:100px; */
      
      display: flex;
      justify-content: center;
      flex-wrap:wrap;
      line-height: 16px;
      margin-bottom:22px;
    }
    .colorstylechild{
      width:100%;
    }
    .el-main {
      background-color: #E9EEF3;
